    return React.createElement("form", { onSubmit: props.handleSubmit },
        React.createElement("div", null, createField('Enter your message', 'newMessageBody', [required, maxLength50], TextArea)),
        React.createElement("div", null,
            React.createElement("button", null, "Send message")));
